'use client';

import Image from 'next/image';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation, Pagination, Autoplay } from 'swiper/modules';



import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import { title } from 'process';

const data = [
  { id: '1', image: '/assets/image-carrossel/image-1-AII.png', title: 'Frango Assado', description: 'Sabor inconfundível, direto do forno.' },
  { id: '2', image: '/assets/image-carrossel/image-2-corretora.png', title: 'Sobrecoxa Especial', description: 'Perfeita para seu almoço em família.' },
  { id: '3', image: '/assets/image-carrossel/image-3-adminis.png', title: 'Tulipa Crocante', description: 'O petisco ideal para qualquer ocasião.' },
  { id: '4', image: '/assets/image-carrossel/image-4-banco.png', title: 'Frango Assado', description: 'Sabor inconfundível, direto do forno.' },
  { id: '5', image: '/assets/image-carrossel/image-5-gestora.png', title: 'Sobrecoxa Especial', description: 'Perfeita para seu almoço em família.' },
  { id: '6', image: '/assets/image-carrossel/image-6-corretora.png', title: 'Tulipa Crocante', description: 'O petisco ideal para qualquer ocasião.' },
]
const Carrossel = () => {
  return (
    <div className="w-full px-4 py-6">
      <Swiper
        modules={[Navigation, Pagination, Autoplay]}
        slidesPerView={3}
        spaceBetween={6}
        autoplay={{ delay: 3000, disableOnInteraction: false }}
        loop
        className="w-full h-[300px]"
      >
        {data.map((item) => (
          <SwiperSlide key={item.id}>
            <div className="relative w-full h-[300px] rounded-xl overflow-hidden shadow-lg group">
              <Image
                src={item.image}
                alt={`Imagem ${item.id}`}
                fill
                className="object-cover transition-transform duration-500 group-hover:scale-110"
              />
            </div>
          </SwiperSlide>
        ))}
      </Swiper>
    </div>
  );
};

export default Carrossel;
